.advantages {
  margin-bottom: 53px;
}

.advantages__title {
  margin: 0 0 42px 0;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  color: #000000;
}

.advantages-list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  list-style-type: none;
}

.advantages-list__item {
  display: flex;
}

.advantages-list__item:not(:last-child) {
  margin-bottom: 20px;
}

.advantages-list__item-description {
  margin: 0;
  font-family: "Arial";
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}
